
.container {
    width: 100%;
    height: 100%;
  }
  
  .suffix {
    position: absolute;
    right: 2px;
    bottom:-3px;
    font-size: 10px; /* Make it small */
    color: darkgrey;
  }

  .tooltip-container {
    position: relative;
    display: inline-block;
    cursor: pointer;
    padding: 10px;
    background: #ddd;
    border-radius: 5px;
  }
  
  .custom-tooltip {
    position: absolute;
    bottom: 120%;
    left: 50%;
    transform: translateX(-50%);
    background-color: black;
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    white-space: nowrap;
    opacity: 0;
    transition: opacity 0.3s;
    pointer-events: none;
  }
  
  .tooltip-container:hover .custom-tooltip {
    opacity: 1;
  }